.landing {
    background-color: black;
    display: flex;
    justify-content: center;
    height: 100vh;
    font-family: Inconsolata, monospace;
    font-size: x-large;
    color: rgb(42, 192, 23);


    .content {
        width: 90%;

        p {
            white-space: nowrap;
            animation: type 9s steps(60, end);
            overflow: hidden;
        }

        .fa-level-down-alt {
            transform: rotate(90deg)
        }

        form {
            input[type="text"] {
                background: transparent;
                outline-width: 0; 
                border: none;
                caret-color: rgb(42, 192, 23);
                margin-left: 10px;
                color: rgb(42, 192, 23);

            }
        }
    }
}

@keyframes type {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}